AEA;QACI,gBAAgB;QAChB,iBAAiB;IACrB;;IAEA;;QAEI,aAAa;IACjB;;IAEA;QACI,kBAAkB;QAClB,uBAAuB;IAC3B;AACJ;;AAEA,qCAAqC;AACrC;IACI;QACI,qCAAqC;QACrC,SAAS;IACb;;IAEA;QACI,iCAAiC,EAAE,8BAA8B;IACrE;AACJ;;AAEA,2CAA2C;AAC3C;IACI;QACI,iBAAiB;IACrB;;IAEA;QACI,qCAAqC;IACzC;AACJ;;AAEA,0CAA0C;AAC1C;IACI;QACI,iBAAiB;IACrB;AACJ;;AAEA,sBAAsB;AACtB;IACI;;;QAGI,0CAA0C;QAC1C,4BAA4B;IAChC;AACJ;;AAEA,oCAAoC;AACpC;IACI;QACI,iBAAiB;IACrB;;IAEA;QACI,iBAAiB;IACrB;;IAEA;QACI,YAAY;IAChB;;IAEA;QACI,UAAU,EAAE,4CAA4C;IAC5D;AACJ;;AAEA,yCAAyC;AACzC;IACI;;;QAGI,qCAAqC;QACrC,uCAAuC;QACvC,sCAAsC;QACtC,gCAAgC;IACpC;;IAEA;QACI,2BAA2B;IAC/B;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,eAAe;IACnB;AACJ;;AAEA,+BAA+B;AAC/B;IACI;QACI,sBAAsB;IAC1B;;IAEA;;;QAGI,+BAA+B;QAC/B,mBAAmB;IACvB;AACJ;;AAEA,iBAAiB;AACjB;IACI;;;;QAII,aAAa;IACjB;;IAEA;QACI,UAAU;IACd;;IAEA;QACI,mBAAmB;QACnB,gBAAgB;QAChB,sBAAsB;IAC1B;;IAEA;;QAEI,WAAW;IACf;;IAEA;QACI,iBAAiB;QACjB,YAAY;IAChB;AACJ","sourcesContent":["/* Enhanced Responsive Homepage Styles */\r\n\r\n/* CSS Reset and Base Styles */\r\n*,\r\n*::before,\r\n*::after {\r\n    box-sizing: border-box;\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\nbody {\r\n    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;\r\n    background-color: #f0f4f8;\r\n    color: #333;\r\n    line-height: 1.6;\r\n    display: flex;\r\n    flex-direction: column;\r\n    min-height: 100vh;\r\n    overflow-x: hidden; /* Prevent horizontal scroll */\r\n}\r\n\r\n/* Container with fluid responsive design */\r\n.container {\r\n    max-width: 1200px;\r\n    margin: 0 auto;\r\n    padding: 0 clamp(0.75rem, 2.5vw, 1.5rem);\r\n    width: 100%;\r\n}\r\n\r\n/* Header - Fully Responsive */\r\n.header {\r\n    background: linear-gradient(135deg, #2563eb, #1e40af);\r\n    color: white;\r\n    padding: clamp(0.75rem, 2vh, 1.5rem) 0;\r\n    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);\r\n    position: sticky;\r\n    top: 0;\r\n    z-index: 1000;\r\n}\r\n\r\n.header-content {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    gap: 1rem;\r\n}\r\n\r\n.header-title {\r\n    font-size: clamp(1.25rem, 4vw, 2.25rem);\r\n    font-weight: 700;\r\n    border-radius: 0.5rem;\r\n    padding: clamp(0.375rem, 1vw, 0.5rem) clamp(0.75rem, 2vw, 1rem);\r\n    background-color: rgba(29, 78, 216, 0.3);\r\n    backdrop-filter: blur(10px);\r\n    border: 1px solid rgba(255, 255, 255, 0.1);\r\n    white-space: nowrap;\r\n    overflow: hidden;\r\n    text-overflow: ellipsis;\r\n    max-width: 70%;\r\n}\r\n\r\n.menu-button {\r\n    display: none;\r\n    color: white;\r\n    border: none;\r\n    background: rgba(255, 255, 255, 0.1);\r\n    cursor: pointer;\r\n    outline: none;\r\n    padding: 0.5rem;\r\n    border-radius: 0.5rem;\r\n    transition: all 0.3s ease;\r\n    min-width: 44px; /* Touch target size */\r\n    min-height: 44px;\r\n}\r\n\r\n.menu-button:hover {\r\n    background: rgba(255, 255, 255, 0.2);\r\n    transform: scale(1.05);\r\n}\r\n\r\n.menu-button:focus {\r\n    outline: 2px solid #93c5fd;\r\n    outline-offset: 2px;\r\n}\r\n\r\n.menu-button svg {\r\n    width: clamp(1.25rem, 3vw, 1.5rem);\r\n    height: clamp(1.25rem, 3vw, 1.5rem);\r\n    display: block;\r\n}\r\n\r\n/* Navigation Menu - Enhanced Mobile Support */\r\n.main-menu {\r\n    background-color: #1d4ed8;\r\n    color: white;\r\n    padding: 1rem 0;\r\n    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);\r\n    transition: all 0.3s ease;\r\n    margin-bottom: 0; /* Remove any margin */\r\n}\r\n\r\n.main-menu-content {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    gap: clamp(1rem, 3vw, 2rem);\r\n    flex-wrap: wrap;\r\n    position: relative;\r\n}\r\n\r\n.nav-link {\r\n    padding: 0.75rem 1rem;\r\n    border-radius: 0.5rem;\r\n    transition: all 0.3s ease;\r\n    font-size: clamp(0.9rem, 2.5vw, 1.125rem);\r\n    font-weight: 500;\r\n    text-decoration: none;\r\n    color: inherit;\r\n    white-space: nowrap;\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 0.5rem;\r\n    min-height: 44px; /* Touch target */\r\n    justify-content: center;\r\n}\r\n\r\n.nav-link:hover {\r\n    background-color: rgba(30, 64, 175, 0.8);\r\n    transform: translateY(-2px);\r\n}\r\n\r\n.nav-link:focus {\r\n    outline: 2px solid #93c5fd;\r\n    outline-offset: 2px;\r\n}\r\n\r\n/* Enhanced Dropdown Menu */\r\n.dropdown {\r\n    position: relative;\r\n    display: inline-block;\r\n}\r\n\r\n.dropdown-toggle {\r\n    cursor: pointer;\r\n}\r\n\r\n.dropdown-icon {\r\n    width: 1rem;\r\n    height: 1rem;\r\n    transition: transform 0.3s ease;\r\n    margin-left: 0.25rem;\r\n}\r\n\r\n.dropdown:hover .dropdown-icon {\r\n    transform: rotate(180deg);\r\n}\r\n\r\n.dropdown-content {\r\n    display: none;\r\n    position: absolute;\r\n    top: 100%;\r\n    left: 50%;\r\n    transform: translateX(-50%);\r\n    background-color: #1d4ed8;\r\n    min-width: clamp(180px, 25vw, 220px);\r\n    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);\r\n    z-index: 1000;\r\n    border-radius: 0.75rem;\r\n    overflow: hidden;\r\n    margin-top: 0.5rem;\r\n    border: 1px solid rgba(255, 255, 255, 0.1);\r\n    backdrop-filter: blur(10px);\r\n}\r\n\r\n.dropdown-content a {\r\n    color: white;\r\n    padding: 0.75rem 1rem;\r\n    text-decoration: none;\r\n    display: block;\r\n    transition: all 0.3s ease;\r\n    font-size: clamp(0.85rem, 2vw, 0.95rem);\r\n    border-bottom: 1px solid rgba(255, 255, 255, 0.1);\r\n    min-height: 44px;\r\n    display: flex;\r\n    align-items: center;\r\n}\r\n\r\n.dropdown-content a:last-child {\r\n    border-bottom: none;\r\n}\r\n\r\n.dropdown-content a:hover {\r\n    background-color: rgba(30, 64, 175, 0.8);\r\n    padding-left: 1.5rem;\r\n}\r\n\r\n.dropdown:hover .dropdown-content {\r\n    display: block;\r\n    animation: fadeInUp 0.3s ease;\r\n}\r\n\r\n@keyframes fadeInUp {\r\n    from {\r\n        opacity: 0;\r\n        transform: translateX(-50%) translateY(-10px);\r\n    }\r\n    to {\r\n  