}

.footer-text {
    font-size: 0.9rem;
    margin: 0;
}

.footer-links {
    display: flex;
    gap: 1.5rem;
}

.footer-link {
    color: white;
    text-decoration: none;
    font-size: 0.9rem;
    transition: color 0.3s ease;
}

.footer-link:hover {
    color: #93c5fd;
}

/* Responsive Design */

/* Tablet */
@media (max-width: 1024px) {
    .container {
        padding: 0 1.5rem;
    }
    
    .main-menu-content {
        gap: 1.5rem;
    }
    
    .dropdown-content {
