    .dropdown-content a:hover {
        padding-left: 2.5rem;
    }
    
    .article-header,
    .intro-section,
    .info-section {
        padding: 1.5rem;
    }
    
    .footer-content {
        flex-direction: column;
        text-align: center;
        gap: 1rem;
    }
    
    .footer-links {
        justify-content: center;
    }
}
