;QACjB,yBAAyB;IAC7B;;IAEA;QACI,eAAe;IACnB;;IAEA;;;QAGI,aAAa;IACjB;;IAEA;QACI,eAAe;QACf,mBAAmB;IACvB;;IAEA;QACI,kBAAkB;IACtB;;IAEA;QACI,eAAe;QACf,gBAAgB;IACpB;;IAEA;QACI,iBAAiB;QACjB,oBAAoB;QACpB,iBAAiB;IACrB;;IAEA;QACI,iBAAiB;IACrB;;IAEA;QACI,sBAAsB;QACtB,WAAW;IACf;AACJ;;AAEA,wBAAwB;AACxB;IACI;QACI,kBAAkB;IACtB;;IAEA;QACI,kBAAkB;IACtB;;IAEA;;;QAGI,gBAAgB;IACpB;;IAEA;QACI,eAAe;IACnB;;IAEA;QACI,iBAAiB;IACrB;AACJ;;AAEA,sBAAsB;AACtB;IACI;QACI,0CAA0C;QAC1C,4BAA4B;IAChC;AACJ;;AAEA,iBAAiB;AACjB;IACI;;;QAGI,aAAa;IACjB;;IAEA;QACI,UAAU;IACd;;IAEA;QACI,gBAAgB;QAChB,sBAAsB;IAC1B;;IAEA;QACI,WAAW;IACf;;IAEA;QACI,WAAW;IACf;;IAEA;QACI,eAAe;QACf,wBAAwB;IAC5B;AACJ;;AAEA,+BAA+B;AAC/B;IACI;;;QAGI,qCAAqC;QACrC,uCAAuC;QACvC,sCAAsC;IAC1C;AACJ;;AAEA,yCAAyC;AACzC;;;IAGI,0BAA0B;IAC1B,mBAAmB;AACvB","sourcesContent":["/* Reset and Base Styles */\r\n* {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n}\r\n\r\nbody {\r\n    font-family: 'Inter', sans-serif;\r\n    background-color: #f0f4f8;\r\n    color: #333;\r\n    line-height: 1.6;\r\n    display: flex;\r\n    flex-direction: column;\r\n    min-height: 100vh;\r\n}\r\n\r\n.container {\r\n    max-width: 1200px;\r\n    margin: 0 auto;\r\n    padding: 0 1rem;\r\n    width: 100%;\r\n}\r\n\r\n/* Header Styles */\r\n.header {\r\n    background: linear-gradient(135deg, #2563eb, #1e40af);\r\n    color: white;\r\n    padding: 1rem 0;\r\n    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);\r\n    position: sticky;\r\n    top: 0;\r\n    z-index: 1000;\r\n  position: sticky;\r\n}\r\n\r\n.header-content {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n}\r\n\r\n.header-title {\r\n    font-size: clamp(1.5rem, 4vw, 2.25rem);\r\n    font-weight: 700;\r\n    border-radius: 0.5rem;\r\n    padding: 0.5rem 1rem;\r\n    background-color: rgba(29, 78, 216, 0.3);\r\n    backdrop-filter: blur(10px);\r\n    border: 1px solid rgba(255, 255, 255, 0.1);\r\n}\r\n\r\n.menu-button {\r\n    display: none;\r\n    color: white;\r\n    border: none;\r\n    background: rgba(255, 255, 255, 0.1);\r\n    cursor: pointer;\r\n    outline: none;\r\n    padding: 0.5rem;\r\n    border-radius: 0.5rem;\r\n    transition: background-color 0.3s ease;\r\n}\r\n\r\n.menu-button:hover {\r\n    background: rgba(255, 255, 255, 0.2);\r\n}\r\n\r\n.menu-button svg {\r\n    width: 1.5rem;\r\n    height: 1.5rem;\r\n    display: block;\r\n}\r\n\r\n/* Navigation Menu */\r\n.main-menu {\r\n    background-color: #1d4ed8;\r\n    color: white;\r\n    padding: 1rem 0;\r\n    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);\r\n    transition: all 0.3s ease;\r\n}\r\n\r\n.main-menu-content {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    gap: 2rem;\r\n    flex-wrap: wrap;\r\n}\r\n\r\n.nav-link {\r\n    padding: 0.75rem 1rem;\r\n    border-radius: 0.5rem;\r\n    transition: all 0.3s ease;\r\n    font-size: 1rem;\r\n    font-weight: 500;\r\n    text-decoration: none;\r\n    color: inherit;\r\n    white-space: nowrap;\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 0.5rem;\r\n}\r\n\r\n.nav-link:hover {\r\n    background-color: rgba(30, 64, 175, 0.8);\r\n    transform: translateY(-2px);\r\n}\r\n\r\n/* Dropdown Menu */\r\n.dropdown {\r\n    position: relative;\r\n    display: inline-block;\r\n}\r\n\r\n.dropdown-toggle {\r\n    cursor: pointer;\r\n}\r\n\r\n.dropdown-icon {\r\n    width: 1rem;\r\n    height: 1rem;\r\n    transition: transform 0.3s ease;\r\n}\r\n\r\n.dropdown:hover .dropdown-icon {\r\n    transform: rotate(180deg);\r\n}\r\n\r\n.dropdown-content {\r\n    display: none;\r\n    position: absolute;\r\n    top: 100%;\r\n    left: 50%;\r\n    transform: translateX(-50%);\r\n    background-color: #1d4ed8;\r\n    min-width: 200px;\r\n    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);\r\n    z-index: 1000;\r\n    border-radius: 0.75rem;\r\n    overflow: hidden;\r\n    margin-top: 0.5rem;\r\n    border: 1px solid rgba(255, 255, 255, 0.1);\r\n    backdrop-filter: blur(10px);\r\n}\r\n\r\n.dropdown-content a {\r\n    color: white;\r\n    padding: 0.75rem 1rem;\r\n    text-decoration: none;\r\n    display: block;\r\n    transition: all 0.3s ease;\r\n    font-size: 0.9rem;\r\n    border-bottom: 1px solid rgba(255, 255, 255, 0.1);\r\n}\r\n\r\n.dropdown-content a:last-child {\r\n    border-bottom: none;\r\n}\r\n\r\n.dropdown-content a:hover {\r\n    background-color: rgba(30, 64, 175, 0.8);\r\n    padding-left: 1.5rem;\r\n}\r\n\r\n.dropdown:hover .dropdown-content {\r\n    display: block;\r\n    animation: fadeInUp 0.3s ease;\r\n}\r\n\r\n@keyframes fadeInUp {\r\n    from {\r\n        opacity: 0;\r\n        transform: translateX(-50%) translateY(-10px);\r\n    }\r\n    to {\r\n        opacity: 1;\r\n        transform: translateX(-50%) translateY(0);\r\n    }\r\n}\r\n\r\n/* Main Content */\r\n.main-content {\r\n    flex-grow: 1;\r\n    padding: 2rem 0;\r\n}\r\n\r\n.content-article {\r\n    background: white;\r\n    border-radius: 1rem;\r\n    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);\r\n    overflow: hidden;\r\n}\r\n\r\n.article-header {\r\n    padding: 2rem;\r\n    background: linear-gradient(135deg, #f8fafc, #e2e8f0);\r\n}\r\n\r\n.section-title {\r\n    font-size: clamp(1rem, 5vw, 2rem);\r\n    font-weight: 800;\r\n    margin-bottom: 1.5rem;\r\n    color: #1e40af;\r\n    text-align: center;\r\n    line-height: 1.2;\r\n}\r\n\r\n.hero-image {\r\n    display: flex;\r\n    justify-content: center;\r\n    margin-top: 1.5rem;\r\n}\r\n\r\n.responsive-image {\r\n    width: 100%;\r\n    max-width: 600px;\r\n    height: auto;\r\n    border-radius: 1rem;\r\n    box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);\r\n    transition: transform 0.3s ease;\r\n}\r\n\r\n.responsive-image:hover {\r\n    transform: scale(1.02);\r\n}\r\n\r\n/* Content Sections */\r\n.intro-section,\r\n.info-section {\r\n    padding: 2rem;\r\n    border-bottom: 1px solid #e5e7eb;\r\n}\r\n\r\n.info-section:last-child {\r\n    border-bottom: none;\r\n}\r\n\r\n.section-subtitle {\r\n 