ponsive Design */\r\n\r\n/* Tablet */\r\n@media (max-width: 1024px) {\r\n    .container {\r\n        padding: 0 1.5rem;\r\n    }\r\n    \r\n    .main-menu-content {\r\n        gap: 1.5rem;\r\n    }\r\n    \r\n    .dropdown-content {\r\n        min-width: 180px;\r\n    }\r\n}\r\n\r\n/* Add this to your existing CSS - Mobile dropdown fix */\r\n\r\n/* Mobile Large */\r\n@media (max-width: 768px) {\r\n    .menu-button {\r\n        display: block;\r\n    }\r\n    \r\n    /* Fix mobile menu positioning */\r\n    .main-menu {\r\n        display: none;\r\n        position: absolute;\r\n        top: 100%;\r\n        left: 0;\r\n        right: 0;\r\n        background-color: #1d4ed8;\r\n        z-index: 999;\r\n        box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);\r\n        margin: 0;\r\n        padding: 0;\r\n    }\r\n    \r\n    /* Ensure header stays sticky and contains the mobile menu */\r\n    .header {\r\n        position: sticky;\r\n        top: 0;\r\n        z-index: 1000;\r\n    }\r\n    \r\n    /* Fix for mobile menu visibility */\r\n    .main-menu.show {\r\n        display: block !important;\r\n        animation: slideDown 0.3s ease;\r\n    }\r\n    \r\n    @keyframes slideDown {\r\n        from {\r\n            opacity: 0;\r\n            transform: translateY(-10px);\r\n        }\r\n        to {\r\n            opacity: 1;\r\n            transform: translateY(0);\r\n        }\r\n    }\r\n    \r\n    .main-menu-content {\r\n        flex-direction: column;\r\n        align-items: stretch;\r\n        gap: 0;\r\n        padding: 1rem 0;\r\n    }\r\n    \r\n    .nav-link {\r\n        padding: 1rem;\r\n        border-bottom: 1px solid rgba(255, 255, 255, 0.1);\r\n        border-radius: 0;\r\n        justify-content: space-between;\r\n    }\r\n    \r\n    .nav-link:last-child {\r\n        border-bottom: none;\r\n    }\r\n    \r\n    /* FIXED: Mobile dropdown positioning */\r\n    .dropdown-content {\r\n        position: static;\r\n        display: none;\r\n        box-shado